.icon{position:relative;object-fit:contain;image-rendering:pixelated;pointer-events:all;padding:0 2px;filter:brightness(30%)}.icon.button{cursor:pointer}.icon:hover{filter:brightness(80%)}.icon.active{filter:brightness(100%)}

.app-container{display:flex;flex-direction:column;position:relative;color:#eee;z-index:1;width:100%}.app-container.inverted{flex-direction:column-reverse}.app-container.resizing{background-color:rgba(1,1,1,0.5)}

.bar-container{position:absolute;top:1px;left:0;width:100%;height:calc(100% - 3px);z-index:0}.bar{height:100%;transition:width 0.5s}

.navbar-tools,.navbar-container{display:flex;flex-direction:row;flex-shrink:0;align-items:center}.navbar-tools{flex-direction:row-reverse;flex:1;margin-right:3px}.navbar-container{height:22px;background-color:#000000d0}.navbar-title{display:flex;align-items:center;padding:0 5px;height:12px;font-size:14px;font-family:'Accidental Presidency';background-color:white;color:black;text-shadow:none;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;word-break:break-all}

.player-container{display:flex;flex-direction:row;align-items:center;width:100%;letter-spacing:1px;font-size:13px;height:20px;background-color:rgba(0,0,0,0.4)}.player-info-container{display:flex;flex:1;justify-self:center;padding-left:4px;height:100%;position:relative;background-image:url("./assets/img/background-dots.png");background-repeat:repeat;background-color:rgba(1,1,1,0.3);overflow:hidden}.player-info-container.you{text-shadow:0px 0px 6px #fe0}.player-position{flex-shrink:0;position:relative;top:1px;z-index:1}.player-name{text-transform:capitalize;overflow:hidden;text-overflow:ellipsis;word-break:break-all;white-space:nowrap;position:relative;top:1px;z-index:1}.player-icon{object-fit:cover;background-color:black;width:22px;height:100%}.player-numbers{display:flex;flex:1;flex-direction:row-reverse;padding:0 4px;word-break:break-all;white-space:nowrap;position:relative;top:1px;z-index:1}

.player-list{display:flex;flex-direction:column;margin-bottom:-2px;overflow:hidden}

.resize-handler-vertical,.resize-handler{background-color:rgba(1,1,1,0.01);position:absolute;z-index:100}.resize-handler-vertical.disabled,.resize-handler.disabled{-webkit-app-region:drag;display:none}.resize-handler-vertical.enabled,.resize-handler.enabled{-webkit-app-region:no-drag}.resize-handler{width:100%;height:7px;bottom:0;cursor:n-resize}.resize-handler-vertical{width:7px;height:calc(100% - 22px);right:0;top:22px;cursor:e-resize}.resize-handler.inverted{bottom:unset;top:0}.resize-handler-vertical.inverted{left:0;right:unset}

.settings-container{display:flex;flex-direction:column;font-family:'Accidental Presidency';width:100%;overflow-x:hidden;overflow-y:auto}.settings-container-item,.settings-container-group{display:flex;align-items:center;color:white;height:20px;flex-shrink:0;background-color:#363636;position:relative;word-break:break-all;white-space:nowrap;text-overflow:ellipsis}.settings-container-group-icon{display:flex;background-color:#000;height:100%;padding:0 2px}.settings-container-group-title{font-size:16px;padding-left:3px;position:relative;top:-1px}.settings-container-item{height:18px;background-color:#141414}.settings-container-item:not(:last-child){border-bottom:1px solid #353535}.settings-container-item:nth-child(odd){background-color:#1d1d1d}.settings-container-item-icon{position:relative;top:-9px;width:16px;height:100%;border-bottom:dashed 1px white;border-left:dashed 1px white;margin-left:12px}.settings-container-item-title{display:flex;align-items:center;font-size:14px;padding-left:5px;margin-bottom:-1px;flex:1;height:100%;cursor:pointer;position:relative;top:-1px}.settings-container-item-title.active{background-color:#ffffff;color:black;text-shadow:none;cursor:auto}.settings-container-item-title:not(.active):hover{background-color:#3d3d3d}

@font-face{font-family:'Accidental Presidency';src:url("./assets/fonts/AccidentalPresidency.woff2") format("woff2"),url("./assets/fonts/AccidentalPresidency.woff") format("woff");font-weight:normal;font-style:normal}

html{width:100%;height:100%}body{width:100%;margin:0;text-rendering:optimizeLegibility;touch-action:manipulation;-ms-content-zooming:none;word-wrap:break-word;overscroll-behavior-y:none;text-size-adjust:none;-webkit-app-region:drag;display:flex}h1,h2,h3,h4{margin:0}main{display:flex;width:245px;height:100px;position:relative;-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-font-smoothing:antialiased !important;text-shadow:#000 0px 1px 1px !important;font-size:12px !important;font-family:'Accidental Presidency' !important}*{user-select:none;box-sizing:border-box;-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none}img{pointer-events:none}a{text-decoration:none}::-webkit-scrollbar{width:8px;background:#1b1a1a;-webkit-appearance:none}::-webkit-scrollbar-thumb{width:8px;height:2px;min-height:2px !important;background:rgba(185,185,185,0.315)}.hidden{display:none !important}

